import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse, HttpResponse } from '@angular/common/http';
import { Subject } from 'rxjs/Subject';
import { Observable } from 'rxjs/observable';
import { of } from 'rxjs/observable/of';
import { catchError } from 'rxjs/operators';
import { environment } from '../../../environments/environment';
import { GitDataAdapter } from './git-data-adapter';
import { FileInfo } from '../models/file';
import { assgin } from 'lodash';

// access environment.SERVER_IP
const SERVER_IP: string = environment.SERVER_IP;
const GIT_BASE_PATH = '/api/dev/main/v1.0/git';
const GIT_INIT = '/init';
const GIT_STATUS = '/status';
const GIT_COMMIT = '/commit';
const GIT_RESET = '/reset';
const GIT_ADD = '/add';
const GIT_BRANCH = '/branch';
const GIT_CHECKOUI = '/checkout';
const GIT_PULL = '/pull';
const GIT_PUSH = '/push';
const GIT_ADDR = '/addr';
const GIT_REMOTE = '/remote';
const GIT_REV_PARSE = '/revparse';

@Injectable()
export class GitDataService {

  messages: string[] = [];

  // TypeScript 构造器语法同时定义参数和属性。
  // TypeScript's constructor syntax for declaring parameters and properties simultaneously.
  // 注入Http服务
  constructor(private http: HttpClient) { }

  /**
   * 获取git revparse信息
   */
  public getGitRevParse(projectPath: string, revInfo: string): Observable<any> {
    const http = this.http;
    const url = this.getGitRevParseUrl(projectPath, revInfo);

    const gitRevParseSubject = new Subject<string>();
    http.get(url, { responseType: 'text' }).subscribe(
      result => {
        // async send git revparse info
        gitRevParseSubject.next(result.toString());
      }
    );

    return gitRevParseSubject;
  }

  /**
   * 获取远程仓库
   * git remote
   */
  public getGitRemote(projectPath: string, remoteName: string, verbose: boolean = false): Subject<any> {
    const http = this.http;
    let url = this.getGitRemoteUrl(projectPath);

    if (undefined !== remoteName  &&  null !== remoteName && remoteName.length > 0 ) {
      url += '&remoteName=' + remoteName;
    }

    url += '&verbose=' + verbose;

    const gitRemoteSubject = new Subject<any>();
    http.get(url, { observe: 'response'}).subscribe(
      result => {
        console.log('git remote result is:' + result);
        gitRemoteSubject.next(result);
      }
    );
    return gitRemoteSubject;
  }

  /**
   * 添加远程仓库
   * git remote add <name> <url>
   */
  public postGitRemoteAdd(projectPath: string, brachName: string, remoteUrl: string, branches: string[] = []): Observable<any> {
    const url = this.getGitRemoteUrl(projectPath);

    const body = {
      name: brachName,
      url: remoteUrl,
      branches: branches,
    };

    return this._postGitCommandWithOptions(url, body, { observe: 'response' });
  }

  /**
   * 初始化Git存储库
   * git init
   * @param projectPath
   */
  public postGitInit(projectPath: string): Observable<any> {
    const url = this.getGitInitUrl(projectPath);
    return this._postGitCommand(url, null);
  }

  /**
   * 发布分支
   * git push -u origin master
   * @param projectPath
   */
  public postReleaseBranch(projectPath: string, remoteReposity: string, branchName: string,
    userName: string, passWord: string): Observable<any> {
    return this.postGitPush(projectPath, remoteReposity, branchName, userName, passWord);
  }

  /**
   * 向指定仓库push
   * git push
   * @param projectPath
   */
  public postGitPush(projectPath: string, remoteReposity?: string, branchName?: string,
    userName?: string, passWord?: string, refspecArray?: string[]): Observable<any> {
    if (projectPath == null || projectPath.length < 0) {
      console.log('Project path is null or empty.');
      return;
    }
    const url = this.getGitPushUrl(projectPath);

    const body = {
      remote: remoteReposity,
      branchname: branchName,
      username: userName,
      password: passWord
    };

    return this._postGitCommandWithOptions(url, body, { observe: 'response' });
  }

  /**
   * pull指定工程
   * git pull
   * @param projectPath
   * @param filePath
   */
  public postGitPull(projectPath: string, userName?: string, passWord?: string,
    remoteReposityName ?: string, isRebase?: boolean): Observable<any> {
    const url = this.getGitPullUrl(projectPath);

    const body = {
      remote: remoteReposityName,
      rebase: isRebase,
      username: userName,
      password: passWord
    };

    return this._postGitCommandWithOptions(url, body, { observe: 'response' });
  }
  /**
   * checkout命令
   * git checkout branchName
   * @param projectPath
   * @param branchName
   * @param createNewBranch
   * @param chosenfiles
   * @param forceRecoverFile
   */
  public postGitCheckout(projectPath: string, branchName: string, createNewBranch: boolean = false,
    chosenfiles: string[] = [], forceRecoverFile: boolean = false): Observable<any> {
    const url = this.getGitCheckoutUrl(projectPath);

    const body = {
      name: branchName,
      createnew: createNewBranch,
      files: chosenfiles,
      force: forceRecoverFile
    };

    return this._postGitCommand(url, body);
  }

  /**
   * 获取Git branch数据
   * @param projectPath 工程所在路径
   */
  public getGitBranchData(projectPath: string = ''): Subject<BranchData[]> {

    const http = this.http;
    const url = this.getGitBranchUrl(projectPath);

    const gitBranchDataSubject = new Subject<BranchData[]>();
    http.get(url).subscribe(
      result => {
        console.log('gitBranchArray' + result);
        const branchData: BranchData[] = this.getAdaptedGitBranchData(result);
        if (branchData.length > 0) {
          gitBranchDataSubject.next(branchData);
        }
      }
    );
    return gitBranchDataSubject;
  }

  public getAdaptedGitBranchData(result: any): BranchData[] {
    const branchArray: BranchData[] = [];

    if (result && result.length > 0) {
      result.forEach(element => {
        const branch: BranchData = new BranchData(element);
        branchArray.push(branch);
      });
    }

    return branchArray;
  }

  /**
   * 添加指定文件到暂存区
   * git add filename
   * @param projectPath
   * @param filePath
   */
  public postGitAdd(projectPath: string, filePath: string): Observable<any> {
    const filesPathArray: string[] = [];
    filesPathArray.push(filePath);

    const url = this.getGitAddUrl(projectPath);
    const body = {
      filePattern: filesPathArray
    };

    return this._postGitCommand(url, body);
  }

  /**
   * 放弃指定文件的更改
   * git checkout -- filename
   * @param projectPath 工程所在路径
   * @param filePath 文件路径信息
   */
  public discardAddedFile(projectPath: string, fileName: string): Observable<any> {

    const files: string[] = [];
    files.push(fileName);

    return this.postGitCheckout(projectPath, '', false, files, true);
  }

  /**
   * 撤销指定的暂存文件
   * git reset HEAD fileName
   * @param projectPath 工程所在路径
   * @param filePath 文件路径信息
   */
  public cancelStagedFile(projectPath: string, filePath: string): Observable<any> {

    const filesPathArray: string[] = [];
    filesPathArray.push(filePath);

    return this.postGitReset(projectPath, 'HEAD', filesPathArray, 'mixed');
  }

  /**
   * 撤销上次Git Commit命令
   * git reset HEAD~2
   * @param projectPath 工程所在路径
   */
  public cancelLastGitCommit(projectPath: string): Observable<any> {
    return this.postGitReset(projectPath, 'HEAD~', [], 'mixed');
  }

  /**
   * 执行Git Reset命令
   * git reset
   * @param projectPath 工程所在路径
   */
  private postGitReset(projectPath: string, localCommit: string = 'HEAD',
    localFilePattern: any[] = [], localMode: string = 'mixed'): Observable<any> {

    const url = this.getGitResetUrl(projectPath);

    const body = {
      commit: localCommit,
      filepattern: localFilePattern,
      mode: localMode // soft|Mixed(default)|hard)
    };

    return this._postGitCommand(url, body);
  }

  /**
   * 推送Git Commit命令
   * @param projectPath 工程所在路径
   * @param commitMessage comit message
   */
  public postGitCommit(projectPath: string, commitMessage: string): Observable<any> {

    const url = this.getGitCommitUrl(projectPath);
    const body = {
      message: commitMessage
    };

    return this._postGitCommand(url, body);
  }

  private _postGitCommand(postUrl: string, body: any): Observable<any> {

    const http = this.http;
    return http.post<any>(postUrl, body)
      .pipe(
        catchError(this.handleError('postGitCommand', postUrl))
      );
  }

  private _postGitCommandWithOptions(postUrl: string, body: any, options: {}): Observable<HttpResponse<any>> {

    const http = this.http;
    return http.post<any>(postUrl, body, options);
  }

  add(message: string) {
    this.messages.push(message);
  }

  clear() {
    this.messages = [];
  }

  /**
   * Returns a function that handles Http operation failures.
   * This error handler lets the app continue to run as if no error occurred.
   * @param serviceName = name of the data service that attempted the operation
   * @param operation - name of the operation that failed
   * @param result - optional value to return as the observable result
   */
  handleError<T>(serviceName = '', operation = 'operation', result = {} as T) {

    return (error: HttpErrorResponse): Observable<T> => {
      // TODO: send the error to remote logging infrastructure
      console.error(error); // log to console instead

      const message = (error.error instanceof ErrorEvent) ?
        error.error.message :
        `server returned code ${error.status} with body "${error.error}"`;

      // TODO: better job of transforming error for user consumption
      this.add(`${serviceName}: ${operation} failed: ${message}`);

      // Let the app keep running by returning a safe result.
      return of(result);
    };

  }

  /**
   * 获取Git Status数据
   * @param projectPath 工程所在路径
   */
  public getGitStatusData(projectPath: string = ''): Subject<FileInfo[]> {

    const http = this.http;
    const url = this.getGitStatusDataUrl(projectPath);
    if (url.length <= 0) {
      return null;
    }

    const gitStatusDataSubject = new Subject<FileInfo[]>();
    http.get(url).subscribe(
      result => {
        // 获取数据后，对数据进行适配
        const gitStatusDataAdapter: GitDataAdapter = new GitDataAdapter(result);

        const gitStatusDataArray: FileInfo[] = gitStatusDataAdapter.getAdaptedGitStatusData();
        console.log('gitStatusDataArray' + gitStatusDataArray);

        if (gitStatusDataArray != null) {
          gitStatusDataSubject.next(gitStatusDataArray);
        }
      }
    );
    return gitStatusDataSubject;
  }

  /**
   * 获取指定路径的git仓库路径，逐级向上查找，没有则返回空
   */
  public getGitAddress(currentPath: string = ''): Subject<string> {
    const http = this.http;
    const url = this.getGitAddrUrl(currentPath);

    const gitAddrSubject = new Subject<string>();
    http.get(url, { responseType: 'text' }).subscribe(
      result => {
        // 获取git仓库的路径信息
        console.log('gitAddr:' + result);
        gitAddrSubject.next(result.toString());
      }
    );

    return gitAddrSubject;
  }

  private _getGitCommand(postUrl: string, body: any): Observable<any> {

    const http = this.http;
    return http.post<any>(postUrl, body)
      .pipe(
        catchError(this.handleError('getGitCommand', postUrl))
      );
  }

  /**
   * get git addr web api
   * @param currentRelativePath
   */
  private getGitAddrUrl(currentRelativePath: string): string {
    const localRelativePath = currentRelativePath.replace(/\\/g, '/');
    let url: string = SERVER_IP + GIT_BASE_PATH + GIT_ADDR;

    if (0 === localRelativePath.length) {
      return;
    }

    url += '?wsPath=' + localRelativePath;

    return url;
  }

  /**
   * get git revParse(parse revision names) web api
   * https://stackoverflow.com/questions/15798862/what-does-git-rev-parse-do
   * @param projectPath
   */
  private getGitRevParseUrl(projectPath: string, revInfo: string): string {
    let url = this._getGitBaseUrl(projectPath, GIT_REV_PARSE);
    return url += '&revision=' + revInfo;
  }

  /**
   * get git remote web api
   * @param projectPath
   */
  private getGitRemoteUrl(projectPath: string): string {
    return this._getGitBaseUrl(projectPath, GIT_REMOTE);
  }

  /**
   * get git init web api
   * @param projectPath
   */
  private getGitInitUrl(projectPath: string): string {
    return this._getGitBaseUrl(projectPath, GIT_INIT);
  }

  /**
   * get git push web api
   * @param projectPath
   */
  private getGitPushUrl(projectPath: string): string {
    return this._getGitBaseUrl(projectPath, GIT_PUSH);
  }

  /**
   * get git pull web api
   * @param projectPath
   */
  private getGitPullUrl(projectPath: string): string {
    return this._getGitBaseUrl(projectPath, GIT_PULL);
  }

  /**
   * get git checkout web api
   * @param projectPath
   */
  private getGitCheckoutUrl(projectPath: string): string {
    return this._getGitBaseUrl(projectPath, GIT_CHECKOUI);
  }

  /**
   * get git branch web api
   * @param projectPath
   * @param listMode
   */
  private getGitBranchUrl(projectPath: string, listMode: string = '0'): string {
    let url: string = this._getGitBaseUrl(projectPath, GIT_BRANCH);
    return url += '&listMode=' + listMode;
  }

  /**
   * get git add web api
   * @param projectPath
   */
  private getGitAddUrl(projectPath: string): string {
    return this._getGitBaseUrl(projectPath, GIT_ADD);
  }

  /**
   * get git reset web api
   * @param projectPath
   */
  private getGitResetUrl(projectPath: string): string {
    return this._getGitBaseUrl(projectPath, GIT_RESET);
  }

  /**
   * get git commit web api
   * @param projectPath
   */
  private getGitCommitUrl(projectPath: string): string {
    return this._getGitBaseUrl(projectPath, GIT_COMMIT);
  }

  /**
   * get git status web api
   * @param projectPath
   */
  private getGitStatusDataUrl(projectPath: string): string {
    return this._getGitBaseUrl(projectPath, GIT_STATUS);
  }

  private _getGitBaseUrl(projectPath: string, operator: string): string {
    let url: string = SERVER_IP + GIT_BASE_PATH + operator;

    if (projectPath.length <= 0) {
      return '';
    }

    url += '?projectPath=' + projectPath;

    return url;
  }

}

export class BranchData {

  _fullName: string;
  _displayName: string;
  _activeBranch: boolean;
  _remoteBranch: boolean;

  constructor({ name, displayName, active, remote }) {
    this._fullName = name;
    this._displayName = displayName;
    this._activeBranch = active;
    this._remoteBranch = remote;
  }

  get fullName() {
    return this._fullName;
  }

  get displayName() {
    return this._displayName;
  }

  get active() {
    return this._activeBranch;
  }

  get remote() {
    return this._remoteBranch;
  }
}



