import { Component } from '@angular/core';

@Component({
    selector: 'git-pull-dialog',
    templateUrl: './git-pull-dialog.component.html',
    styleUrls: ['./git-pull-dialog.component.css']
})

export class GitPullDialogComponent {
  gitPullName: string;
  get GitPullName(){
    return this.gitPullName;
  }

  gitPullPassword: string;
  get GitPullPassword(){
    return this.gitPullPassword;
  }
}



