import { Component } from '@angular/core';

@Component({
    selector: 'git-remote-add-dialog',
    templateUrl: './git-remote-add-dialog.component.html',
    styleUrls: ['./git-remote-add-dialog.component.css']
})
// http://www.ruanyifeng.com/blog/2014/06/git_remote.html
export class GitRemoteAddDialogComponent {
    remoteReposityUrl: string;
    get RemoteReposityUrl(){
      return this.remoteReposityUrl;
    }

    remoteReposityName: string;
    get RemoteReposityName(){
      return this.remoteReposityName;
    }
}



