import { ComponentFactoryResolver, Injector, Type } from '@angular/core';

import { GSP, Package } from '@farris/ide-devkit';

import { GitPanelComponent } from './views/git-panel/git-panel.component';
import { GitPanelPackageView } from './views';
import { GitPanelState } from './views/git-panel/git-panel.state';

const id = 'gitPanel';

export class GitPanelPackage extends Package {
  layoutPanel: any;
  modelPanel: any;
  subscriptions: null;
  gitPanelState: GitPanelState;

  constructor(gsp: GSP, private cfr: ComponentFactoryResolver, private injector: Injector) {
    super(gsp);
  }

  activate(state) {

    this.packageView = new GitPanelPackageView(state.gitPanelPackageViewState, this.cfr, this.injector);

    this.layoutPanel = this.gsp.workspace.getLeftDock().getActivePane().addItem({
      id: id,
      getTitle: () => 'Git',
      getElement: () => this.packageView.getElement()
    }, {});

    // 处理file-tree-panel上的事件
    this.gitPanelState = this.injector.get(GitPanelState);
    this.gitPanelState.gitInit.subscribe((data) => this.gitInitHandler(data));

    this.gsp.eventBus.on(null, null, 'CurrentGitReposity', this,
      (gitReposityPath: string) => this.updateGitProjectPath(gitReposityPath));

    this.gsp.eventBus.on(null, null, 'FocusedNodePathInfo', this,
      (focusedNodePathInfo: string) => this.updateFocusedNodePathInfo(focusedNodePathInfo));

    this.gsp.eventBus.on(null, null, 'CurrentGitBranch', this,
      (gitBranchPath: string) => this.updateGitBranchPath(gitBranchPath));
  }

  private gitInitHandler(data) {
    this.gsp.eventBus.post('GitPanelPackage', this.constructor.name, 'GitInit', data);
  }


  private updateGitProjectPath(gitReposityPath) {
    const gitPanelComponent = this.packageView.getElement().instance as GitPanelComponent;
    gitPanelComponent.setGitProjectPath(gitReposityPath);
  }

  private updateFocusedNodePathInfo(pathInfo) {
    const gitPanelComponent = this.packageView.getElement().instance as GitPanelComponent;
    gitPanelComponent.setFocusedNodePathInfo(pathInfo);
  }

  private updateGitBranchPath(gitBranchInfo) {
    const gitPanelComponent = this.packageView.getElement().instance as GitPanelComponent;
    gitPanelComponent.setGitBranchPath(gitBranchInfo);
  }

  deactivate() {
    this.packageView.destroy();
    console.log('GitPanelPackageView deactivated. Need deserialize.');
  }

  serialize() {
    return {
      gitPanelPackageViewState: this.packageView.serialize()
    };
  }

  toggle() {
    // 布局面板需要提供toggle？
    // return (
    //   this.modelPanel.isVisible() ?
    //     this.modelPanel.hide() :
    //     this.modelPanel.show()
    // );
  }

  getComponent(): Type<any> {
    return GitPanelComponent;
  }
}



