export class FileInfo {
    // fileType: string;
    _fileName: string;
    _filePath: string;
    _fileStatus: string;
    _fileTracked: boolean;
    _fileStaged: boolean;

    constructor({ fileName, filePath, fileStatus, fileTracked, fileStaged }) {
        this._fileName = fileName;
        this._filePath = filePath;
        this._fileStatus = fileStatus;
        this._fileTracked = fileTracked;
        this._fileStaged = fileStaged;
    }

    get fileName() {
        return this._fileName;
    }

    get filePath() {
        return this._filePath;
    }

    get fileStatus() {
        return this._fileStatus;
    }

    get fileTracked() {
        return this._fileTracked;
    }

    get fileStaged() {
        return this._fileStaged;
    }

}



