import { Component } from '@angular/core';

@Component({
    selector: 'git-release-branch-dialog',
    templateUrl: './git-release-branch-dialog.component.html',
    styleUrls: ['./git-release-branch-dialog.component.css']
})

export class GitReleaseBranchDialogComponent {
  userName: string;
  get UserName(){
    return this.userName;
  }

  password: string;
  get Password(){
    return this.password;
  }
}



