import { Component } from '@angular/core';

@Component({
    selector: 'git-push-dialog',
    templateUrl: './git-push-dialog.component.html',
    styleUrls: ['./git-push-dialog.component.css']
})

export class GitPushDialogComponent {
  gitPushName: string;
  get GitPushName(){
    return this.gitPushName;
  }

  gitPushPassword: string;
  get GitPushPassword(){
    return this.gitPushPassword;
  }
}



